
.Carditem {
    border: 1px solid rgb(218, 218, 218);
    width: 23%;
    margin: 1rem;
    padding: 1rem;
    box-shadow: 0px 1px 3px rgb(218, 218, 218);
    height: max-content;

    
    button{
        all: unset;
        cursor: pointer;
    }

    .Btns{
        height: 5%;
        display: flex;
        justify-content: flex-end;
        gap: 10px;
        align-items: baseline;
        
    }

    .TitleYearFilm{
        height: 14%;
        display: flex;
        align-items: baseline;

        h2 {
            color: black;
        }

        p {
            padding-left: 0.5rem;
            color: rgb(120, 120, 120);
        }
    }

    .ImgFilm {
        
        max-width: 214px;
        height: auto;
        max-height: 318px;


        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
            background-color: aqua;
            border: none;
        }
    }

    .DescriptionTitle {
        height: 25%;
        overflow: hidden;

    }

    ul {
        li {
        list-style-type: none;}
    }

    .Genres {
        height: 5%;
        font-style: italic;
        color: rgb(120, 120, 120);


        ul {
            display: flex;
            gap: 10px;
            padding: 0;
            margin: 0;
            border-radius: 20px;
            display: inline-block;

           li {
                background-color: #EFEFEF;
                padding: 0.3rem;
                display: inline-block;
                margin: 0.2rem;
                border-radius: 4px;
            }
        }
    }
}